<template>
  <div>
    <div class="row">
      <div class="col-lg-1">
        <el-button type="success" @click="isAddLog = true"></el-button>
      </div>
    </div>
    <el-table :data="userWords" style="width: 100%" stripe height="520">
      <el-table-column prop="name" label="" width="300"></el-table-column>
      <el-table-column prop="time" label="" width="400"></el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" circle @click="deleteTimes(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog
      title="Word"
      :visible.sync="isAddLog"
      width="80%"
      :before-close="CloseAddLog"
      :show-close="false"
    >
      <form enctype="multipart/form-data" id="formAdd">
        <div class="row">
          <div class="col-lg-12">
            <el-input type="text" placeholder="()" name="fileName" v-model="wordName" />
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <!-- :
    type:
    editable
    format
    value-format:
            -->
            <el-date-picker
              id="dateAddLog"
              v-model="wordTime"
              type="datetime"
              placeholder=""
              :editable="false"
              format="yyyy  MM  dd "
              value-format="yyyyMMdd"
              name="fileTime"
            ></el-date-picker>
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <el-tag type="success">word:</el-tag>
            <input type="file" id="file_file" name="fileWord" />
          </div>
        </div>
      </form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onAddLog(0)"> </el-button>
        <el-button type="primary" @click="onAddLog(1)">
          
          <i class="el-icon-upload el-icon--right"></i>
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getDate();
  },
  data() {
    return { isAddLog: false, userWords: [], wordTime: "", wordName: "" };
  },
  methods: {
    getDate() {
      const resUserWord = this.$http.post("getUserWord");
      resUserWord.then(r => {
        const words = r.data.data;
        for (let i = 0; i < words.length; i++) {
          let dataClass = words[i].split("$:");
          this.userWords.push({
            name: dataClass[0],
            time: dataClass[1]
          });
        }
      });
    },
    CloseAddLog() {
      //
      $("#file_file").val("");
      this.wordTime = "";
      this.wordName = "";
    },
    onAddLog(is) {
      ///
      if (is == 0) {
        //
        this.isAddLog = false;
      } else {
        //
        let name = this.wordName;
        let time = this.wordTime;
        let file = $("#file_file");
        if (name == "" || time == "" || file.val() == "") {
          this.$message.warning("!");
        } else {
          var formData = new FormData(document.getElementById("formAdd")); //form
          const addApk = this.$http.post("addUserWord", formData);
          const loading = this.$animationLoad();
          addApk.then(r => {
            loading.close();
            this.isAddLog = false;
            this.$message.success(r.data.msg);
            this.$router.go(0);
          });
        }
      }
    },
    deleteTimes(data) {
      //apk
      if (confirm("" + data.name + "")) {
        //
        const deletImage = this.$http.post("setUserWord", {
          wordName: data.name
        });
        const loading = this.$animationLoad();
        deletImage.then(r => {
          loading.close();
          this.$message.success(r.data.msg);
          this.$router.go(0);
        });
      }
    }
  }
};
</script>
<style scoped lang = 'less'>
#formAdd {
  div {
    margin-top: 10px;
    .col-lg-12 .el-tag {
      margin-right: 10px;
    }
  }
}
</style>