<template>
  <div>
    <div class="row">
      <div class="col-lg-1">
        <el-button type="success" id="addTimes" @click="isAddLog = true"></el-button>
      </div>
    </div>

    <el-table :data="userTimes" style="width: 100%" stripe height="520">
      <el-table-column prop="time" label="" width="180"></el-table-column>
      <el-table-column prop="text" label="" width="600"></el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" circle @click="deleteTimes(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="isAddLog"
      width="80%"
      :before-close="CloseAddLog"
      :show-close="false"
    >
      <!-- :
    type:
    editable
    format
    value-format:
      -->
      <el-date-picker
        id="dateAddLog"
        v-model="chTime"
        type="datetime"
        placeholder=""
        :editable="false"
        format="yyyy  MM  dd "
        value-format="yyyyMMdd"
      ></el-date-picker>
      <el-input type="textarea" :rows="5" placeholder="" v-model="dateText"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onAddLog(0)"> </el-button>
        <el-button type="primary" @click="onAddLog(1)"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getData();
  },
  data() {
    return { userTimes: [], isAddLog: false, chTime: "", dateText: "" };
  },
  methods: {
    getData() {
      //
      const resUserTimes = this.$http.post("getUserTings");
      resUserTimes.then(r => {
        this.userTimes = r.data.data;
      });
    },
    deleteTimes(data) {
      //
      if (confirm("" + data.time + "")) {
        //
        const deletTimes = this.$http.post("setUserTings", {
          text: data.text
        });
        const loading = this.$animationLoad();
        deletTimes.then(r => {
          loading.close();
          this.$message.success(r.data.msg);
          this.$router.go(0);
        });
      }
    },
    CloseAddLog() {
      //
      this.chTime = "";
      this.dateText = "";
    },
    onAddLog(is) {
      ///
      if (is == 0) {
        //
        this.isAddLog = false;
      } else {
        //
        if (this.dateText != "" && this.chTime != "") {
          const addTimes = this.$http.post("addUserTings", {
            text: this.dateText,
            time: this.chTime,
            user_id: "0"
          });
          const loading = this.$animationLoad();
          addTimes.then(r => {
            loading.close();
            this.$message.success(r.data.msg);
            this.isAddLog = false;
            this.$router.go(0);
          });
        } else {
          this.$message.warning("!");
        }
      }
    }
  }
};
</script>
<style scoped lang="less">
#addTimes {
  margin-left: 10px;
}
.el-textarea {
  margin-top: 10px;
}
</style>