<template>
  <div>
    <div class="row">
      <div class="col-lg-1">
        <el-button type="success" id="addTimes" @click="isAddLog = true"></el-button>
      </div>
    </div>
    <el-table :data="userImages" style="width: 100%" stripe height="520">
      <el-table-column prop="image" label="" width="180">
        <template slot-scope="scope">
          <el-image style="width: 100px; height: 100px" :src="scope.row.path"></el-image>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="" width="600"></el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" circle @click="deleteTimes(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="isAddLog"
      width="80%"
      :before-close="CloseAddLog"
      :show-close="false"
    >
      <form enctype="multipart/form-data" id="formAdd">
        <el-alert title=":home,mainBg," type="warning"></el-alert>
        <div class="row">
          <div class="col-lg-12">
            <el-input type="text" placeholder="()" name="fileName" v-model="imageName" />
          </div>
        </div>
        <div class="row">
          <div class="col-lg-12">
            <input type="file" id="file_image" name="fileImage" />
          </div>
        </div>
      </form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onAddLog(0)"> </el-button>
        <el-button type="primary" @click="onAddLog(1)">
          
          <i class="el-icon-upload el-icon--right"></i>
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getDate();
  },
  data() {
    return {
      userImages: [],
      isAddLog: false,
      imageUrl: "",
      imageName: ""
    };
  },
  methods: {
    getDate() {
      //
      //
      const resUserImg = this.$http.post("getUserImg");
      resUserImg.then(r => {
        const images = r.data.data;
        //
        for (let i = 0; i < images.length; i++) {
          this.userImages.push({
            name: images[i],
            path: this.$Http.httpImage + images[i]
          });
        }
      });
    },
    CloseAddLog() {
      //
      this.imageName = "";
      $("#file_image").val("");
    },
    onAddLog(is) {
      //
      if (is == 0) {
        this.isAddLog = false;
      } else {
        //:jqury
        let name = this.imageName;
        let file = $("#file_image");
        if (name == "" || file.val() == "") {
          this.$message.warning("!");
        } else {
          //
          var formData = new FormData(document.getElementById("formAdd")); //form
          const addImg = this.$http.post("addUserImg", formData);
          const loading = this.$animationLoad();
          addImg.then(r => {
            loading.close();
            this.isAddLog = false;
            this.$message.success(r.data.msg);
            this.$router.go(0);
          });
        }
      }
    },
    deleteTimes(data) {
      //
      if (confirm("" + data.name + "")) {
        //
        const deletImage = this.$http.post("setUserImg", {
          imgName: data.name
        });
        const loading = this.$animationLoad();
        deletImage.then(r => {
          loading.close();
          this.$message.success(r.data.msg);
          this.$router.go(0);
        });
      }
    }
  }
};
</script>
<style scoped lang="less">
#formAdd {
  div {
    margin-top: 10px;
  }
}
</style>