<template>
  <div class="container marketing">
    <!--  -->
    <el-table :data="userData" style="width: 100%" stripe height="580">
      <el-table-column prop="type" label="" width="180"></el-table-column>
      <el-table-column prop="value" label="" width="400"></el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showUpdateLog(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog
      id="updateLog"
      title=""
      :visible.sync="isUpdateLog"
      width="80%"
      :before-close="CloseUpdateLog"
      :show-close="false"
    >
      <el-input v-model="update_value" placeholder=""></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onUpDataLog(0)"> </el-button>
        <el-button type="primary" @click="onUpDataLog(1)"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getData();
  },
  data() {
    return {
      mainUserData: [], //
      updateKey: "", //
      userData: [],
      isUpdateLog: false, //
      update_value: "" //
    };
  },
  methods: {
    showUpdateLog(data) {
      //
      this.isUpdateLog = true;
      this.updateKey = data.type;
    },
    onUpDataLog(is) {
      ///
      if (is == 0) {
        this.isUpdateLog = false;
        this.update_value = "";
      } else {
        switch (this.updateKey) {
          case "":
            this.mainUserData[0].name = this.update_value;
            break;
          case "":
            this.mainUserData[0].quotes = this.update_value;
            break;
          case "QQ":
            this.mainUserData[0].qq = this.update_value;
            break;
          case "":
            this.mainUserData[0].wx = this.update_value;
            break;
          case "":
            this.mainUserData[0].phone = this.update_value;
            break;
          case "":
            this.mainUserData[0].email = this.update_value;
            break;
          case ":":
            this.mainUserData[0].loveText = this.$getCodePwd(this.update_value);
            break;
          case ":":
            this.mainUserData[0].serviceText = this.$getCodePwd(
              this.update_value
            );
            break;
          case "":
            this.mainUserData[0].note = this.update_value;
            break;
        }
        //
        const resData = this.$http.post("setUserData", this.mainUserData[0]);
        const loading = this.$animationLoad();
        resData.then(r => {
          loading.close();
          this.$message.success("!");
          this.isUpdateLog = false;
          this.$router.go(0);
        });
      }
    },
    CloseUpdateLog() {
      //
      this.update_value = "";
    },
    getData() {
      //
      const resUserData = this.$http.post("getUserData");
      resUserData.then(r => {
        const data = r.data.data;
        this.mainUserData = data;
        this.userData.push({ type: "", value: data[0].name });
        this.userData.push({ type: "", value: data[0].quotes });
        this.userData.push({ type: "QQ", value: data[0].qq });
        this.userData.push({ type: "", value: data[0].wx });
        this.userData.push({ type: "", value: data[0].phone });
        this.userData.push({ type: "", value: data[0].email });
        this.userData.push({
          type: ":",
          value: this.$setCodePwd(data[0].loveText)
        });
        this.userData.push({
          type: ":",
          value: this.$setCodePwd(data[0].serviceText)
        });
        this.userData.push({ type: "", value: data[0].note });
      });
    }
  }
};
</script>
<style scoped>
</style>