<template>
  <div class="container marketing">
    <el-page-header @back="goBack" content=""></el-page-header>
    <!--  -->
    <router-view></router-view>
    <!--  -->
    <img src="@/assets/image/serviceHome_opMenu.png" id="opMenu" @click="openMenu" />
    <!-- 
    directionltr--
    :visible.sync:
    size:
    -->
    <el-drawer
      id="rightDrawer"
      title=""
      :visible.sync="isDrawer"
      direction="ltr"
      size="42%"
      :show-close="false"
      :before-close="closeMenu"
    >
      <!-- 
     default-active:
     class
     
      -->
      <el-menu
        :default-active="brMenu"
        class="el-menu-vertical-demo"
        background-color="white"
        text-color="#000"
        active-text-color="#ffd04b"
      >
        <el-menu-item
          :index="item.name"
          v-for="(item,i) in menus"
          :key="i"
          @click="toServicePage(i)"
        >
          <i :class="item.icon"></i>
          <el-badge v-show="i==menus.length-1" :value="opinionLen"></el-badge>
          <span slot="title" v-show="i!=menus.length-1">{{item.name}}</span>
        </el-menu-item>
      </el-menu>
    </el-drawer>
  </div>
</template>
<script>
export default {
  created() {
    this.initView();
  },
  data() {
    return {
      isDrawer: false,
      menus: [
        { name: "", path: "/serviceMy", icon: "el-icon-user" },
        { name: "", path: "/serviceTimes", icon: "el-icon-s-help" },
        {
          name: "",
          path: "/serviceImages",
          icon: "el-icon-picture-outline"
        },
        {
          name: "apk",
          path: "/serviceApk",
          icon: "el-icon-mobile-phone"
        },
        {
          name: "html",
          path: "/serviceHtml",
          icon: "el-icon-bank-card"
        },
        {
          name: "word",
          path: "/serviceWord",
          icon: "el-icon-notebook-2"
        },
        {
          name: "",
          path: "/serviceOpinion",
          icon: "el-icon-warning-outline"
        }
      ],
      brMenu: "",
      opinionLen: 0
    };
  },
  methods: {
    //
    initView() {
      //
      this.brMenu = window.sessionStorage.getItem("brMenu");
      if (this.brMenu == null || this.brMenu == "") {
        this.brMenu = this.menus[0].name;
      }
      //
      const resUserOpinion = this.$http.post("getUserOpinion");
      resUserOpinion.then(r => {
        this.opinionLen = r.data.data.length;
      });
    },
    //
    toServicePage(i) {
      this.$router.replace(this.menus[i].path);
      this.isDrawer = false;
      window.sessionStorage.setItem("brMenu", this.menus[i].name);
    },
    goBack() {
      //
      this.$router.replace("/home");
    },
    openMenu() {
      //
      this.isDrawer = true;
      //
      $("#opMenu").css({ transform: "rotate(180deg)" });
    },
    closeMenu() {
      //
      this.isDrawer = false;
      //
      $("#opMenu").css({ transform: "rotate(0)" });
    }
  }
};
</script>
<style scoped lang="less">
#opMenu {
  position: absolute;
  top: 80%;
  left: 80%;
  width: 50px;
  height: 50px;
  transition: 0.8s;
  z-index: 100;

  &:hover {
    cursor: pointer;
    width: 45px;
    height: 45px;
    transition: 0.4s;
  }
}
.el-page-header {
  margin: 5%;
}
.el-menu-item {
  text-align: left;
}
</style>